.da-button {

    font-weight: 500;

    &.white-text-link {
        color: $white!important;
    }

    &.da-primary-button {

        background-color: $daYellow;        
        color: $black;
        border-radius: 23px;
        padding: 10px 15px;

        &.da-large-button {
            font-size: rem-calc(20);
        }

        &.da-medium-button {
            font-size: rem-calc(16);
        }

        &.da-small-button {
            font-size: rem-calc(14);
        }

        &:hover {
            background-color: $daYellowHover;
        }

    }

    &.da-secondary-button {
        
        background-color: $daLightGrey;        
        color: $black;
        border-radius: 23px;
        padding: 10px 15px;

        &.da-large-button {
            font-size: rem-calc(20);
        }

        &.da-medium-button {
            font-size: rem-calc(16);
        }

        &.da-small-button {
            font-size: rem-calc(14);
        }

        &:hover {
            background-color: $daDarkGrey;
        }

    }

    &.da-tertiary-button {
                
        color: $black;
        border-radius: 5px;
        padding: 10px 15px;
        border: 2px $daLightBlue solid;

        &.da-large-button {
            font-size: rem-calc(20);
        }

        &.da-medium-button {
            font-size: rem-calc(16);
        }

        &.da-small-button {
            font-size: rem-calc(14);
        }

        &:hover {
            background-color: $daLightBlue;
        }

    }

    &.da-quaternary-button {
        
        background-color: transparent;        
        color: $white;
        border-radius: 5px;
        padding: 10px 15px;
        border: 2px $daLightBlue solid;

        &.da-large-button {
            font-size: rem-calc(20);
        }

        &.da-medium-button {
            font-size: rem-calc(16);
        }

        &.da-small-button {
            font-size: rem-calc(14);
        }

        &:hover {
            background-color: $daLightBlue;
            color: $black;
        }

    }

    &.da-quinary-button {
        
        color: $black;
        border-bottom: 2px $daLightBlue solid;
        padding: 0 0 3px 0;
        //position: relative;

        &:hover {
            &:after {
				//position: absolute;
                font-family: 'FontAwesome';
                font-weight: 300;
                padding-left: 3px;
				//top: 29px;
				//right: 0;
				content: "\f105";
			}
        }
        
    }

}