/******************************
General Single Post Styles
******************************/

.single #single-post-container {

	@include breakpoint(medium) {
	   
	   		    
	    
	}

	@include breakpoint(large) {
	   

	    
	}

	#single-post-title {

		padding: rem-calc(15) 0 0 0;

	}

	#post-social-share {

		@include no-margin-padding();
		text-align: right;

		li {
			display: inline-block;
			list-style: none;
			margin-left: 5px;
			color: $primary-color;

			&:first-child {

				margin-left: 0;

			}

			a {

				color: $primary-color;

			}
		}

	}

	#single-post-nav {

		margin: 40px 0;

		color: $secondary-color;
		font-weight: bold;

		a {

			color: $secondary-color;

		}

	}

}

#single-press-release-container {

 	.container1140 {
 		width: auto;
 	}

 	.single-press-release {
 		margin-bottom: 30px;

 		h5, p {
 			margin-bottom: 0;
 		}
 	}

}