/******************************
General Archive Styles
******************************/

.archive {

	.blog-post-wrapper {

		overflow: hidden;

	}

	.blog-featured-image {

		transition: all .5s ease-in-out;

		&:hover {

			transform: scale(1.05)

		}
	}

}

#post-pagination-wrapper {

	padding: 20px 0;

	.page-numbers {

		&.current {

			color: $primary-color;

		}

		color: $secondary-color;
		font-weight: bold;
		border: 1px solid #e3e3e3;
		padding: 5px;

	}

}

/******************************
Masonry Grid Archive Styles
******************************/

.archive #masonry-container {

	.column-block {

		margin-bottom: 0;

	}

	.gutter-sizer-3-percent {

		width: 3%;

	}

	.masonry-item {

		@include breakpoint(small) {

			margin: 0 0 20px 0;
			padding: 0;
			
		}

		@include breakpoint(large) {

			width: 47%;
			
		}

		border: 1px solid #e3e3e3;
		@include box-shadow(1px, 1px, 1px, rgba(0, 0, 0, 0.05));

		.masonry-item-post-title {

			color: $primary-color;
			margin-bottom: 5%;

		}

		.masonry-item-post-content {

			padding: 5% 5% 0 5%;

		}

		.masonry-item-post-read-more a {
		
			display: block;
			line-height: 3;
			padding: 0 5%;
			background-color: #FAFAFA;
			color: $secondary-color;

		}

	}

}

/******************************
URL Bucket Archive Styles
******************************/

#url-bucket-section {

	#url-bucket-row-first {

		padding-top: 40px;

	}

	.screen-reader-text {
	
		@include make-invisible();
	
	}
	
	.url-bucket-row {
	
		padding-bottom: 40px;

		h1 {

			padding-bottom: 20px;
			padding-left: rem-calc(15);

		}

		h2, .archive-intro-copy {
			padding-left: rem-calc(15);
		}
	
		.url-bucket-post {

			margin-bottom: 15px;

			.url-bucket-post-inner {

				border: 1px solid #cccccc;
				padding: 15px;

				.url-bucket-post-featured {

					margin-bottom: 10px;

				}

			}		
	
		}

		.nav-links {
			text-align: center;

			.page-numbers {
    			padding: 5px 15px;
    			border: 2px solid;
    			font-weight: bold;
			}

		}
	
	}

}

/******************************
FAQ Bucket Archive Styles
******************************/

.post-type-archive-faq h1 {
	padding-left: 0px !important;
}

/******************************
Lists Bucket Archive Styles
******************************/
.post-type-archive-lists ul {
	margin: 0 0 20px 0;
	padding: 0;

	li {
		list-style: none;
	}
}