#infogroup-modal {

	border: 0;
	background-color: transparent;
	padding: 0;
	outline: 0;

	#closeInfogroupModal {
		position: absolute;
		top: 3px;
		right: 20px;
		font-size: rem-calc(30);
		color: $secondary-color;
    	cursor: pointer;
	}

	#areYouSure {
		position: absolute;
		top: 30px;
		right: 18px;
		z-index: 1007;
		display: none;		
		max-width: 280px;

		img {
			position: absolute;
			right: 20px;
		}	

		p {
			border-radius: 5px;
			background-color: #ed1c24;
			padding: 10px;
			color: $white;
			font-weight: bold;
			margin-top: 24px;
		}
	}

}