.hero1, .hero1 * {
  font-size: rem-calc(67.34);
  font-weight: 600;
  line-height: 1.3;
  color: $black;
  font-family: $poppins;
}

.hero2, .hero2 * {
  font-size: rem-calc(50.517);
  font-weight: 600;
  line-height: 1.3;
  color: $black;
  font-family: $poppins;
}

.boldfont {
	font-weight: bold!important;
}

.whitetext, .whitetext * {
  color: $white;

  a {
    color: $secondary-color;

    &:hover {
      color: $daLightOrange;
    }
  }

  .bas-phone {
    font-weight: bold;
  }
}

/******************************
Foundation Off Canvas
******************************/

#offCanvas {

  .close-deal {
    position: absolute;
    bottom: 5px;
    right: 8px;
    font-weight: bold;
  }

  .vc_column-inner {
    padding: 0;
  }

}

/******************************
General Styles
******************************/

/* Use on first row outside of header to give padding */
.outside-wrapper-row {

	padding-top: 40px;

}

#post-social-share li {

	font-size: 1.125rem;

}

.infogroupVideo {
	width: 100% !important;
	height: auto !important;
}

.igIconList {
	margin-left: 0;

  .igIconListItemContent {
    max-width: 95%;
  }
}

.fixedBackground {
    background-attachment: fixed;
}

.disclaimer p {
  font-size: 0.8125rem;
}

#footer-registration-modal, #footer-w2l-modal {
  border-top: 8px solid $primary-color;
}

.reveal {
  .close-button {
    font-size: 30px;
    position: absolute;
    top: 0px;
    right: 8px;
    cursor: pointer;
  }
}

/******************************
Pre & Post Auth Styles
******************************/

.post-auth {

	display: none;

}

/******************************
WordPress Image Alignment Styles
******************************/
img {
    
  	&.centered {
   		display: block;
   		margin-left: auto;
   		margin-right: auto;
   	}
    
   	&.alignright {
   		float: right;
   		padding: 4px;
   		margin: 0 0 2px 7px;
   		display: inline;
   	}

   	&.alignleft {
   		float: left;
   		padding: 4px;
   		margin: 0 7px 2px 0;
   		display: inline;
   	}

}

/******************************
Foundation 6 Accordion Styles
******************************/

.accordion {
  margin-bottom: 2rem;

  .accordion-title:hover,
  .accordion-title:focus {
    color: $white;
  }
}

/******************************
Leadership Bio Modal Styles
******************************/

.bio-visible * {
  cursor: pointer;

  img {
    margin-bottom: rem-calc(20);
  }
}

.bio-modal-hidden {

  .close-button {

    font-size: 30px;
    position: absolute;
    top: 0px;
    right: 8px;
    cursor: pointer;

  }

}

/******************************
Table Styles
******************************/

.iusaTable {

  table {

    width: 100%;
    border: 0;
    border-spacing: 2px 0;
  
    tr {      

      td {
        padding: 15px;
        border: 0;

        &:nth-child(2) {
          font-weight: bold;
        }        
      }

      &:nth-child(even) {
        background-color: #F4F4F4;
      }

      &:nth-child(odd) {
        background-color: #E8E8E8;
      }

      &:first-child td, th {
        background-color: $secondary-color;
        color: $white;
        font-size: rem-calc(18);
        font-weight: bold;
      }      

    }

  }

}

/******************************
Background Gradient Styles
******************************/

.gradient {

  &-orange {
    background: #fa6b07;
    background: linear-gradient(#fa6b07, #ed4d07);

    a {
      @include call-out-bar-anchors();
    }

    &-grid {
      background-image: url(#{$imgPath}/grid-orange.svg), linear-gradient(#fa6b07, #ed4d07);
      background-position: right center,center center;
      background-repeat: repeat-y,repeat;
      background-size: 50%,cover;

      a {
        @include call-out-bar-anchors();
      }

    }

  }

  &-blue {
    background: #f1f4f8;
    background: linear-gradient(to right, $white, #f1f4f8);

    a {
      @include call-out-bar-anchors();
    }

  }

  &-grey {
    background: #4f5153;
    background: linear-gradient(#4f5153, #3b3b3b);

    a {
      @include call-out-bar-anchors();
    }

    &-grid {
      background-image: url(#{$imgPath}/grid-grey.svg), linear-gradient(#4f5153, #3b3b3b);
      background-position: right center,center center;
      background-repeat: repeat-y,repeat;
      background-size: 50%,cover;

      a {
        @include call-out-bar-anchors();
      }

    }

  }

}