/******************************
General Footer Styles
******************************/

#site-footer {

	background-color: $black;
	padding-top: 50px;

	#footer-content {

		p {
			font-size: 1rem;
		}

		h6 {
			color: $white;
			font-family: $roboto;
			font-size: rem-calc(14);
			font-weight: bold;
		}

		.menu > li {
			display: block;
			margin-bottom: 5px;

			a {
				padding: 0;
				line-height: inherit;
			}
		}
	}

	p, a {
		color: $white;
		font-size: 1rem;		
	}

	a:hover {
		color: #5C6B6B;
	}

	#site-copyright {

		background-color: $daLightBlack;

		p {
			line-height: 3;
			margin-bottom: 0;
			font-size: rem-calc(13);
		}
	}

	#footer-logo-block {
		img {
			margin-bottom: 5px;
		}
	}

	#floating-contact-methods {

		#floating-call {

    		position: fixed;
    		bottom: 50px;
    		left: 50px;
    		z-index: 9999999;
    		display: none;
    		width: 241px;
    		height: 65px;
    		background: transparent url(#{$imgPath}/floatingCall.png) no-repeat center center;    
			border-radius: 5px;
			padding: 15px 0 0 64px;

			p {
				text-align: center;
				color: $black;
				font-size: 16px;
				line-height: 16px;
				font-weight: bold;
			}

			span {
				font-size: 11px;
				color: $black;
				font-weight: normal;
			}
	
			a {font-size: 16px;
				color: $black;
				text-decoration: none;
			}		

		}

		#floating-chat {

			display: none;
			position: fixed;
    		bottom: 50px;
    		right: 50px;
    		z-index: 9999999;

		}

	}

	#slide-up-contact-window {

		position: fixed;
		right: 40px;
		bottom: -325px;
		width: 300px;
		z-index: 99999999;
		background-color: $white;
		padding: 20px;
		border-radius: 5px;
		box-shadow: 3px 3px 5px 0 rgba(110,110,110,0.6);

		h6 {
			font-weight: normal;
			font-size: 1.15rem;
			color: $secondary-color;
		}

		p {
			font-size: 0.875rem;
			font-weight: normal;
			color: $secondary-color;
		}

		a {
			color: $white;
			font-size: 1rem;
		}

		#close-sliding-contact-window {
			position: absolute;
			top: 2px;
			right: 3px;
			font-size: 1.4rem;
			color: $secondary-color;
			cursor: pointer;
		}

	}

}