/******************************
Gravity Forms Custom Styles
******************************/

.gform_wrapper {

	.validation_error, .validation_message {
		
		margin-bottom: 15px;
  		color: $alert-color;
  		font-weight: bold;

	}

	.gfield_visibility_hidden {
		display: none;
	}

	.gform_fields { // main <ul> in form

		margin-left: 0;

		li {

			list-style: none;
			margin-bottom: 5px;

			label {
				display: none;
			}

			input {
				@include gravityFormFields();

				&::placeholder {
  					color: #a2a2a2;
  					opacity: 1;  					
				}
			}

			select {
				@include gravityFormFields();	
			}

			&.gform_hidden {
				display: none;
			}

			.warningTextareaInfo, .charleft {
				display: none;
			}			

		}

	}

	.gform_button {
		cursor: pointer;		        
		color: $black;
		background-color:transparent;
        border-radius: 5px;
        padding: 10px 15px;
        border: 2px $daLightBlue solid;

		&:hover {
			background-color: $daLightBlue;
			color: $black;
		}
	}

}

/******************************
Gravity Forms Ad Preview Tool
Specific Custom Styles
******************************/

#sampleAdWrapper {

	padding: 10px;
	border: 1px solid #E0E0E0;

	#sampleAdHeader {

		background: url(../img/adPreviewHamburger.png) 16px 10px no-repeat;

		#sampleAdGoogleLogo {
			max-width: 100px;
			display: block;
			margin: 20px auto;
		}

		#sampleAdSearch {

			box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
			border-radius: 8px;					
			margin-bottom: 10px;

			p {
				@include no-margin-padding();
				border-bottom: 1px solid rgba(0,0,0,.12);
				background: url(../img/adPreviewSearchIcon.png) right center no-repeat;
				-webkit-border-top-left-radius: 8px;
				-webkit-border-top-right-radius: 8px;
				-moz-border-radius-topleft: 8px;
				-moz-border-radius-topright: 8px;
				border-top-left-radius: 8px;
				border-top-right-radius: 8px;
				line-height: rem-calc(40);
				text-indent: 16px;
				color: #3c4043;
			}

			ul {

				@include no-margin-padding();

				li {
					color: rgba(0,0,0,0.54);
					display: inline-block;
					padding: 0 16px;
					font-size: 0.75rem;
					line-height: rem-calc(40);
					text-transform: uppercase;							
				}

			}

		}

	}

	.sampleAd {
		box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
		border-radius: 8px;
		padding: 16px;
		margin-bottom: 10px;

		.sampleAdHeadline {
			color: #1967D2;
			font-size: 1rem;
			line-height: 1.25rem;
			padding-bottom: 0;
			margin-bottom: 0;
		}

		.sampleAdURL {
			color: #006621;
			font-size: rem-calc(14);

			.sampleAdSymbol {
				border: 1px solid #006621;
				border-radius: 3px;
				margin-right: 7px;
				padding: 0 3px;
			}
		}

		hr {
			height: 0;
			border-bottom: 1px solid #DFE1E5;
			margin: rem-calc(12) auto;
		}

		.sampleAdDescription {
			color: #3c4043;
			font-size: rem-calc(14);
			line-height: rem-calc(20);
			padding-bottom: 0;
			margin-bottom: 0;
		}

	}
	
}

.contact-policy-hover {
	color: $primary-color;
	cursor: pointer;
	display: inline-block;
}

.contact-policy-tooltip {
	display: none;
	border-radius: 5px;
	background-color: $black;
	font-size: rem-calc(11);
	width: 200px;
	padding: 10px;	
	position: absolute;
	left: 10px;
	bottom: 10px;
	color: $white;
	z-index: 99999999999;

}






















































