/******************************
General Header Styles
******************************/

#site-header {
	height: auto!important;

	@include breakpoint(large) {

		&.infogroup-sticky-main-nav {
			position: fixed;
			z-index: 99999;
			top: 0;
			background-color: #ffffff;
			width: 100%;
			//text-align: center;
			height: 51px;
			box-shadow: 0 1px 2px 0px $secondary-color;			
		}

	}

}

/******************************
Logobar Styles
******************************/

#logobar {

	@include breakpoint(small) {
    	
    	#main-logo {
			max-width: 50%;
		}

		min-height: 75px;		
		
  	} // end smal break point

  	@include breakpoint(medium) {
    	
    	#main-logo {
			max-width: 100%;
		}
		
  	} // end medium break point

  	@include breakpoint(large) {
    	
    	.title-bar {
			display: none!important;
		}

		#logo-left {

			height: 100%;
			min-height: 75px;
			display: flex;
			flex-direction: row;
			padding-top: 0!important;									
	
			a {
				display: flex;
				flex-direction: column;					
				justify-content:space-around;
				align-content: flex-start;
			}
	
			.title-bar {
				display: flex;
				flex-direction: column;
				justify-content: space-around;
				align-content: flex-end;
				justify-content: flex-end;
	
			}
	
		}
		
  	} // end large break point		

	#logo-left {

		padding-top: 20px;

	}
	
}

/******************************
Main Navbar Styles
******************************/

#main-navbar {

	@include breakpoint(medium) {    	

		
	
  	} // end medium break point

  	@include breakpoint(large) {
		
		#infogroup-main-nav li:hover > a, #infogroup-main-nav li.current-menu-item > a {
			background-color: $daLightGrey;
			border-bottom: 3px solid $daLightBlue;		
		}			

  		#infogroup-main-nav {			

  			z-index: 999;
			height: 100%;
			min-height: 75px;							
			display: flex!important;
			flex-direction: row;
			justify-content: space-between;
			align-items: stretch;
  			
  			&> li {					

				a {
					font-size: rem-calc(13);
					height: 100%;
					padding: 0;
					display: flex;
					flex-direction: column;					
					justify-content:space-around;
					padding-left: 8px;
					padding-right: 8px;
					border-bottom: 3px solid $white;
					font-weight: 500;
					color: $black;
					font-family: $poppins;
					
					&:hover {
						background-color: $daLightGrey;
						border-bottom: 3px solid $daLightBlue;
					}

				}

				ul {
					margin-left: 0;
					background-color: $daLightGrey;
					padding: 0;
					z-index: 999;
				}
			}

  		} 

		.is-dropdown-submenu-item a {
			color: $black;
			padding: 16px 16px!important;
			border: none!important;
			font-size: rem-calc(14)!important;

			&:hover {
				background-color: $daDarkGrey!important;
				border: none!important;
			}
		}

		.is-dropdown-submenu-item:hover > a:after {
			background-color: transparent!important;
		}

		.megamenu > ul {
			left: auto;
			width: auto;

			&> li {
				float: left!important;
				width: 220px!important;
				padding-left: 10px!important;
				padding-right: 10px!important;
				text-align: left;

				&> a {					
					font-weight: bold!important;
				}				

				&> ul {
					position: static;
					margin-left: 10px;
					display: block;
					background-color: transparent!important;				
				}				
			}
		}

  	} // end large break point

  	.register {

  		a {
			color: $black!important;
			background: $daYellow;
			border-radius: 23px;
			max-height: 46px!important;
			padding-left: 16px!important;
			padding-right: 16px!important;
			margin-top: 14px;
			border: none!important;			

			&:hover {
				background-color: $daYellowHover!important;
				border: none!important;
			}

  		}
  	}

  	.login a {
  		color: #0285fc!important;
  	}

  	.promo {
  		background: $primary-color;

  		a {
  			color: $white!important;
  		}
  	}

	.call {

		a {
			padding-left: 20px!important;			
			position: relative;

			&:before {
				position: absolute;
				font-family: 'FontAwesome';
				top: 29px;
				left: 5px;
				content: "\f095";
			}

		}

	}

	.chat {

		a {
			padding-left: 20px!important;			
			position: relative;

			&:before {
				position: absolute;
				font-family: 'FontAwesome';
				top: 29px;
				left: 4px;
				content: "\f086";
			}

		}

	}

  	#infogroup-main-nav.accordion-menu {
  		background-color: $white;
  	}

}